// 购物车
import { mapState } from "vuex";
import { Toast, Header } from "mint-ui";
import vFooter from "@/components/v-footer";
import vNodata from "@/components/v-nodata";
import vHeader from "@/components/v-header";
import del from "@/assets/images/del2.png";
const qs = require("qs");
export default {
  data() {
    return {
      canEdit: true,
      list: [],
      del: del,
      allSelect: false,
      checkallList: [],
      checked: [],
      price:0,
      count:0,
      isShowList: false,
      goodsCarts:[],
      payPrice: 0,
      userCouponIds: [],
      goodsChannel: 1,
      isUseIntegral:0,
      num: '',
      id: this.getUrlParam("id"),
      showAddress: null,
      goodsArr: [],
      goodsTips: "",
      uuid:''
    };
  },
  components: { 
    'v-footer': vFooter,
    "v-nodata": vNodata,
    vHeader
  },
  computed: {
    ...mapState(["token"])
  },
  mounted() {//给window添加一个滚动滚动监听事件
    window.addEventListener('scroll', this.handleScroll, false);
  },
  beforeCreate(){
    document.title = '购物车';
  },
  created() {
    this.getCartList();
  },
  methods: {
    // 改变数量
    changeNum(type, item, items) {
      let num = item.num;
      if (type == "add") {//因为已进入方法就加减过了，现在把数量还原回去
        num += 1;
      }else{
        num -= 1;
      } 
      this.$axios
        .post(
          this.api.otoCartUpdate,
          JSON.stringify({
           'goodsCount': num,
           'id': item.id
          }),
          {
            headers: {
              "content-type": "application/json",
              "Authorization": this.token
            }
          }
        )
        .then(res => {
          console.log(res);
          const resData = res.data;
          if (resData.code !== 1) {
            this.showTip("修改数量失败");
            return;
          }
          if (type == "add") {//因为已进入方法就加减过了，现在把数量还原回去
            item.num += 1;
            items.cartPrice += item.price;
          }else{
            item.num -= 1;
            items.cartPrice -= item.price;
          }
          if(num==0){
            this.goodsArr=[];
            this.getCartList();
          }else{
            this.showTip("修改数量成功");
          }
          
        })
        .catch(res => {
          this.showTip("修改数量失败");
        });
    },
    deleteCart(val){
        this.$axios
        .get(this.api.otoBatchDelete+val,{
          headers: {
            "Authorization": this.token,
            "content-type": "application/json"
          }
        })
        .then(res => {
          const resData = res.data;
          if (resData.code !== 1) {
            this.showTip(resData.msg);
            return;
          }else{
            this.showTip("删除成功");
            this.goodsArr = [];
            this.getCartList();
          }
        })
        .catch(res => {
         // this.showTip("网络错误");
        });
    },
    getCartList(){
      this.$axios
        .post(
          this.api.otoGoodsCartList,
          JSON.stringify({}),
          {
            headers: {
              "content-type": "application/json",
              "Authorization": this.token
            }
          }
        )
        .then(res => {
          const resData = res.data;
          if (resData.code !== 1) {
            this.showTip("未获取到商品信息");
            return;
          }else{
            console.log(resData);
            // 成功后赋值商品对象，并存进数组
            if(resData.content.length>0){
              this.isShowList = true;
              const objData = resData.content;
              for( let items of objData){
                let goodsBuyInfo = [];
                let ids = [];
                let cartPrice = 0;
                for( let item of items.list ){
                  let objGoodsData = {
                  'id':item.id,
                  'goodsId': item.goodsId,
                  'num': item.goodsCount,
                  'name': item.goodsName,
                  'price': item.goodsPrice,
                  'img': item.goodsImg?this.api.urlPic+item.goodsImg.split(',')[0]:''
                  }
                  cartPrice = cartPrice + item.goodsCount * item.goodsPrice;
                  goodsBuyInfo.push(objGoodsData);
                  ids.push(item.id);
                }
                let itemInfo = {
                  "cartPrice": cartPrice,
                  'storeName': items.storeName,
                  'storeId': items.list[0].storeId,
                  "goodsBuyInfo": goodsBuyInfo,
                  "ids": ids.join(",")
                }
               this.goodsArr.push(itemInfo)
              }
              console.log(this.goodsArr);
            }else{
              this.isShowList = false;
            }
          }
        })
        .catch(res => {
         // this.showTip("未获取到商品信息");
        });
    },
    // 结算下单
    makeOrder(id) {
      this.$router.push({//核心语句
        path:'/store/fillOrder',//跳转的路径
        query:{//路由传参时push和query搭配使用 ，作用时传递参数
          storeId: id
        }
      })
    }
  }
};



